*{
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: white;
}
.main{
    display: flex;
    height: 100px;
    background: transparent;
    border: 1px solid white;
    background: linear-gradient(135deg , rgb(245, 181, 255,0.1),rgba(198, 2, 247, 0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}
.logo{
    width: 50%;
    margin-left: 0;
    padding-top: 20px;
    text-align: center;
    justify-content: center;
}
.links{
    width: 50%;
    height: 100px;
    padding-top: 30px;
    margin-right: 0;
    font-family: serif;
    display: flex;
    justify-content: center;
}
.links a{
    margin-left: 50px;
    color: #010938;
    transition: 1s;
}
.links a:hover{
    font-size: 30px;
    text-shadow: 0 0 20px #B6FFFA;
}
.main-frame{
    background-color: #201E43;
    background: url("./Components/Icons/blue.jpeg") no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
}
.home-main{
    display: flex;
    height: 100vh;
}
.detail{
    width: 50%;
    margin: 100px 0 0 100px;
    padding: 50px 100px;
    text-shadow: 0 0 20px #B6FFFA;
    background: linear-gradient(135deg , rgb(245, 181, 255,0.1),rgba(198, 2, 247, 0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    height: 400px;
    width: 100%; /* Ensure the element spans the full width of its container */
    background: linear-gradient(135deg, rgba(245, 181, 255, 0.1), rgba(198, 2, 247, 0));
    border: 1px solid white;
    backdrop-filter: blur(10px); /* Apply blur effect */
    -webkit-backdrop-filter: blur(10px); /* For Safari */
    /* Additional properties to enhance the glass effect */
    border-radius: 8px; /* Optional: rounded corners for a smoother glass effect */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.7); /* Optional: shadow to add depth */
    justify-content: center; /* Center content horizontally */
    align-items: center; /* Center content vertically */
    font-family: Georgia, serif;
    
}
#HEY{
    font-size: 30px;
    margin-top: 10px;
}
#name{
    font-size: 50px;
    font-weight: bold;
    margin-top: 10px;
    font-family: sans-serif;
}
#info{
    font-size: small;
}
#email{
    display: flex;
    color: blue;
    text-decoration: underline;
    margin-top: 20px;
}
.media{
    margin-top: 20px;
}
.media a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border: 2px solid white;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-right: 30px;
    transition: 1s;
    margin-top: 100px;
}
.media i{
    width: 40px;
    height: 40px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}
.media a:hover{
    background-color: #B6FFFA;
    box-shadow: 0 0 20px #B6FFFA;
    border-color: black;
}
.fa-brands:hover{
    color: black;
}
.first-frame{
    width: 500px;
    height: 500px;
    margin:  100px 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, rgba(245, 181, 255, 0.1), rgba(198, 2, 247, 0));
    border: 1px solid white;
    backdrop-filter: blur(10px); /* Apply blur effect */
    -webkit-backdrop-filter: blur(10px); /* For Safari */
    /* Additional properties to enhance the glass effect */
    border-radius: 30%;
    box-shadow: 0 0 20px black;
}
.main-photo{
    width: 500px;
    height: 500px;
    background: url("./Components/Icons/10898329833.png");
    background-size: cover;
    border-radius: 30%;
}
.fillter-color{
    width: 500px;
    height: 500px;
    margin:  100px,100px;
    border-radius: 50%;
    /* background-color: rgba(66, 170, 255, 0.3); */
    background-size: cover;
    
}
.about-button{
    margin-top: 30px;
    width: 200px;
    height: 50px;
    border-radius: 4px;
    background-color: rgb(50, 173, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: 1s;
}
.about-button:hover{
    box-shadow: 0 0 5px rgb(13, 161, 224),0 0 10px rgb(13, 161, 224),0 0 20px rgb(13, 161, 224),0 0 25px rgb(13, 161, 224);
    background: rgb(13, 161, 224);
    color: black;
}
.about-section{
    width: 70%;
    height: 300px;
    color: rgb(8, 1, 92);
    margin: 100px 100px;
    background: linear-gradient(135deg, rgba(245, 181, 255, 0.1), rgba(198, 2, 247, 0));
    border: 1px solid white;
    backdrop-filter: blur(10px); /* Apply blur effect */
    -webkit-backdrop-filter: blur(10px); /* For Safari */
    /* Additional properties to enhance the glass effect */
    border-radius: 30px;
    box-shadow: 0 0 20px black;
    padding: 100px 100px 100px 100px;
    font-family:'Times New Roman', Times, serif;
    font-size: 20px;
    justify-content: space-evenly;
}
.about-section h1{
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.skills-section {
    background-color: #201E43;
    padding: 50px;
    color: white;
    text-align: center;
    height: 100vh;
  }
  
  .skills-content {
    background: linear-gradient(135deg, rgba(245, 181, 255, 0.1), rgba(198, 2, 247, 0));
    border: 1px solid white;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.7);
    padding: 20px;
    font-family: Georgia, serif;
  }
  
  .contact-section {
    background-color: #201E43;
    padding: 50px;
    color: white;
    text-align: center;
    height: 100vh;
  }
  
  .contact-section a {
    color: #00f;
    text-decoration: none;
  }
  
  .contact-section a:hover {
    text-decoration: underline;
  }
  